define('ember-cli-chartjs/models/person', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Person = DS['default'].Model.extend({
    name: DS['default'].attr("string"),
    hobby: DS['default'].attr("string")
  });

  Person.reopenClass({

    FIXTURES: [{
      id: "1",
      name: "leslie",
      hobby: "dancing"
    }, {
      id: "2",
      name: "nathan",
      hobby: "traveling"
    }]
  });

  exports['default'] = Person;

});